.header {
  padding: 20px;
}

.nav-bar {
  width: 50%;
}

.fa-bars {
  display: none;
}

.screen {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 1);
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
  z-index: 100;
}

@media (max-width: 830px) {
  .nav-bar {
    flex-direction: column;
    height: 100vh;
    max-width: 400px;
    width: 50%;
    padding: 0;
    padding-top: 50px;
    position: fixed;
    top: 0;
    right: 0;
    background: rgba(255, 255, 255, 1);
    border-left: 1px solid rgba(153, 153, 153, 0.5);
    transform: translate(100%, 0);
    transition: transform 0.6s;
    z-index: 100;
    margin: 0;
  }
  .openMenu + .nav-bar {
    transform: translate(0, 0);
  }
  .nav-bar {
    color: black;
    .route-nav {
      display: block;
      text-align: center;
      padding: 8px 0;
      color: black !important;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    a {
      display: block;
      text-align: center;
      padding: 0 !important;
      color: black !important;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    li {
      border-bottom: 1px solid rgba(153, 153, 153, 0.5);
      width: 100%;
      margin: 0;
      color: black;
    }
  }
  .nav-bar.route-nav:hover {
    background-color: rgba(204, 204, 204, 0.4);
  }
  .menu-btn {
    display: block;
  }
  .fa-bars {
    opacity: 1;
    color: black;
    font-size: 2rem;
    display: flex;
    align-items: center;
    align-content: center;
    cursor: pointer;
  }
  .openMenu.screen {
    visibility: visible;
    opacity: 0.5;
  }
}

.map {
  width: 300px;
  height: 300px;
}
